import LoadImg from '@/components/LoadImg'
export default {
  name: 'MiniAppQrCodeNoKeep',
  components: {
    LoadImg,
  },
  data () {
    return {
      imgSrc: ''
    }
  },
  created () {
    this.imgSrc = 'http://api.yeba.im/wxmini/bars/' + this.$barId + '/chatroom/qrcode'
  },
  methods: {
    goBack () {
      this.$router.back()
    }
  }

}



