
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
index:0,
Tabbar,
TabbarItem
},
data(){
return {
index:0
}
},
created(){
this.changeIndex()
},
activated(){
this.changeIndex()
},
methods:{
changeIndex(){
let routeName = this.$route.name
this.index = ['HomeHome','HomeGameList','HomeBarSet'].findIndex((item)=>{
return routeName === item
})
console.log(this.index)
}
}
}



