
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import Scroll from '@/components/scroll'
import EventBusMixin from '@/js/mixin/eventBus'
import LoadStatusMixin from '@/js/mixin/loadStaus'
import BarChangeMixin from '@/js/mixin/barChange'
export default {
components: {
Cell,
Group,
CellFormPreview,
Scroll,
XSwitch
},
data() {
return {

}
},
mixins:[EventBusMixin,LoadStatusMixin,BarChangeMixin],
created() {
this.getOccupyImgList()
},
methods: {
changeData(data){
let obj1 = {
label: '套餐时长',
value: data.duration
}
let obj2 = {
label: '套餐价格',
value: data.price
}
let objArr = [obj1, obj2]
if (data.theme) {
objArr.push({
label: '专属主题',
value: data.theme.theme_name
})
}
if (data.remark) {
objArr.push({
label: '备注',
value: data.remark
})
}
Object.assign(objArr, data)
return objArr
},

getOccupyImgList() {
this.onloadStart()
this.$api.getOccupyImg(this.$barId).then((data) => {
this.list = []
for (let i = 0; i < data.length; i++) {
this.list.push(this.changeData(data[i].data))
}
this.onLoadSuccess()
}).catch(err => {
this.onLoadError()
})
},

deleteOccupy(occupyscreen_package_id) {
this.$api.deleteOccupyImg(occupyscreen_package_id).then((res) => {
this.$util.remove(this.list, occupyscreen_package_id)
})
},
onEventBus(data){
if (data.type === 'occupyImgEdit') {
this.$util.change1(this.list, this.changeData(data.data))
} else if (data.type === 'occupyImgAdd') (
this.list.unshift(this.changeData(data.data))
)
},
onBarChange(){
this.getOccupyImgList()
},
onChange(item){
let status = item.status === 0?1:0
let data = {
status
}
this.$api.updateOccupyScreen(item.id, data).then((item) => {
this.$util.change1(this.list, this.changeData(item))
})
}
}
}



