
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import MyGrid from '../../components/MyGrid'
import MySet from '../../components/MySet'
export default {
name: 'Home',
data() {
return {
list:[]
}
},
components:{
MyGrid,
MySet,
Group,
Cell,
Swiper
},
created(){
this.getADs()
},
methods:{
getADs(){
this.$api.getADs()
.then(data=>{
let imgs = []
for (let item of data){
imgs.push({
img:item.image,
url:item.url,
title:item.slide_name
})
}
this.list = imgs
})
}
}
}



