
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import BarChangeMixin from '@/js/mixin/barChange'
export default {
components: {
Cell,
Group,
XSwitch
},
data () {
return {
status: 2,
}
},
mixins:[BarChangeMixin],
methods: {
getGoodsOpen(){
this.$api.getBarConfigs(this.$barId,'bar_wine').then((data) => {
this.status = data[0].status
this.id = data[0].id
})
},
onClick (Val) {
let status = this.status === 1 ? 2:1
let data = {
'bar_id': this.$barId,
'status': status,
'cfg_modules_name': 'bar_wine',
'cfg_type': 1
}
this.$api.updateModule(this.id, data).then((res) => {
this.status = status
})
},
onBarChange(){
this.getGoodsOpen()
}
},
created () {
this.getGoodsOpen()
}
}



