export default {
  name: 'LoadImg',
  data () {
    return {}
  },
  props: {
    url: {
      type: String
    },
    urls: {
      type: Array
    },
    borderRadius:{
      type:String
    }
  },
  methods:{
    onImgClick(){
      let urls
      if (this.urls&&this.urls.length>0){
        urls = this.urls
      }else {
        urls = [this.url]
      }
      if (this.$util.judgeWeiXin()){
        window.wx.previewImage({
          current:this.url,
          urls
        })
      }else {
        this.$router.push({
          name: 'BigPicView',
          params: {
            urls,
            url:this.url
          }
        })
      }
    }
  }
}



