
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import BarChangeMixin from '@/js/mixin/barChange'

export default {
components: {
Group,
Cell,
FlexboxItem,
Flexbox
},
data () {
return {
showFinal: false
}
},
created () {
this.getFinal()
},
mixins: [BarChangeMixin],
methods: {
getFinal () {
let userId = this.$store.state.userInfo.id
let barId = this.$barId
this.$api.getFinal(barId, userId)
.then(data => {
this.showFinal = !!(data && data.length > 0);
})
},
onBarChange () {
this.showFinal = false
this.getFinal()
},
}
}



