

import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import router from './router/index'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import store from './vuex/store'
import util from './js/util'
import api from './js/api/index'
import config from './config/config'
import VueLazyload from 'vue-lazyload'
import logo from './assets/logo.svg'
import VueClipboard from 'vue-clipboard2'

Vue.prototype.$util = util
Vue.prototype.$config = config
Vue.prototype.$api = api

Vue.use(VueRouter)
Vue.use(LoadingPlugin)
Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(VueClipboard)
Vue.use(ConfirmPlugin)
Vue.use(VueLazyload, {
error: logo,
loading: logo
})
Vue.use(require('vue-wechat-title'))
Vue.config.productionTip = config.DEV

initFunDebug()

function initFunDebug () {
let fundebug = require('fundebug-javascript')
fundebug.apikey = 'efe68fd84675429ee7b11a0f626cb6e66ad9212b85165a787f432eacc6c2c596'
fundebug.appversion = config.VERSION
fundebug.releasetage = config.RELEASESTAGE
fundebug.silent = (process.env.NODE_ENV === 'development')
fundebug.filters = [
{
message: /cancel delete/
},
{
req: {
method: /^POST$/,
url: /signature/
},
res: {
status: /401/
}
}
]
Vue.config.errorHandler = function (err, vm, info) {
let componentName = formatComponentName(vm)
let propsData = vm.$options && vm.$options.propsData
fundebug.notifyError(err, {
metaData: {
componentName,
propsData,
info
}
})
}
}

function formatComponentName (vm) {
if (vm.$root === vm) return 'root'
let name = vm._isVue ? (vm.$options && vm.$options.name) || (vm.$options && vm.$options._componentTag) : vm.name
return (name ? 'component <' + name + '>' : 'anonymous component') + (vm._isVue && vm.$options && vm.$options.__file ? ' at ' + (vm.$options && vm.$options.__file) : '')
}

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: {App}
})



