
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import Scroll from '@/components/scroll'
import EventBusMixin from '@/js/mixin/eventBus'
import LoadStatusMixin from '@/js/mixin/loadStaus'
import BarChangeMixin from '@/js/mixin/barChange'

export default {
components: {
Cell,
Group,
CellFormPreview,
Scroll
},
data () {
return {}
},
mixins: [EventBusMixin, LoadStatusMixin, BarChangeMixin],
methods: {
changeData (data) {
let price = data.unit_price
let crops = data.vote_crops
let Acount = crops[0].vote_count    
let Bcount = crops[1].vote_count   
let Amount = (Acount + Bcount) * price 
let info = [
{
label: "启动时间",
value: data.begin_time
},
{
label: "截止时间",
value: data.end_time
},
{
label: "A战队票数",
value: Acount
},
{
label: "B战队票数",
value: Bcount
},
{
label: "票单价",
value: price
},
{
label: "总收入",
value: Amount
},
];
Object.assign(info, data)
return info
},
getvotelist () {
this.$api.getVote(this.$barId).then((data) => {
this.onloadStart()
let list = []
for (let i = 0; i < data.length; i++) {
list.push(this.changeData(data[i]))
}
this.list = list
this.onLoadSuccess()
}).catch(err => {
this.onLoadError()
})
},
onEventBus (data) {
if (data.type === 'voteEdit') {
this.$util.change1(this.list, this.changeData(data.data))
} else if (data.type === 'voteAdd') (
this.list.unshift(this.changeData(data.data))
)
},
onBarChange () {
this.getvotelist()
}
},
created () {
this.getvotelist();
}
}



