
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import MyFoot from '../../components/MyFoot.vue'
import BarChangeMixin from '@/js/mixin/barChange'
import MyInput from '@/components/MyInput'
export default {
components: {
Group,
MyInput,
MyFoot,
CellFormPreview,
},
mixins:[BarChangeMixin],
data () {
return {
user_id: '',
list: []
}
},
created () {
this.getBarHrList()
},
methods: {

getBarHrList () {
this.$api.getBarHr(this.$barId).then((data) => {
this.setHr(data)
})
},

addHr () {
if (this.$util.judgeValue(this.user_id,'请输入用户id')){
return
}
let data = {
'bar_id': this.$barId,
'user_id': this.user_id
}
this.$api.updateBarHr(data).then((data) => {
this.user_id = ''
this.setHr(data)
})
},
setHr(data){
if (data.length===0)
return
let obj1 = {
label: '夜吧ID',
value: data.user_id
}
let obj2 = {
label: '昵称',
value: data.user.nickname
}
this.list = [obj1, obj2]
}
},
onBarChange(){
this.getBarHrList()
}
}



