
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import MyFoot from '../../components/MyFoot.vue'
import MyInput from '@/components/MyInput'

let params
export default {
name:'DiceConditionAddNoKeep',
components: {
Group,
MyInput,
PopupPicker,
MyFoot
},
data () {
return {
value1: [],
list: [],
condition: ''
}
},
created () {
this.getPrizes()
params = this.$route.params
if (params.id) {
this.condition = params.condition
this.list = [[{
name: params.prize_name + '(￥' + params.prize_price + ')',
value: params.prize_id + ''
}]]
this.value1[0] = params.prize_id + ''
}
},
methods: {

getPrizes () {
this.$api.getPrizesList(this.$barId,1).then((data) => {
let list1 = []
for (let i = 0; i < data.length; i++) {
let obj = data[i]
let prize = {
name: obj.prize_name + '(￥' + obj.prize_price + ')',
value: obj.id + ''
}
list1.push(prize)
}
if (list1.length>0){
this.list.splice(0,1,list1)
}
})
},

addPrize (e) {
if (e) {
if (this.$util.judgeValue(this.condition, '请输入Y币值')){
return
}
if (this.$util.judgeValue(this.value1.length>0&&this.value1[0]!=='0','请选择奖品')){
return
}
let data = {
'bar_id':this.$barId,
'prize_id': parseInt(this.value1[0]),
'condition': parseInt(this.condition),
}
if (params.id) {
this.$api.editDicePrize(this.$barId, params.id, data).then((item) => {
this.$store.commit(this.$config.STORAGE.EVENT_BUS, {
type: 'diceConditionEdit',
data: item
})
this.$router.back()
})
} else {
this.$api.addDicePrize(this.$barId, data).then((item) => {
this.$store.commit(this.$config.STORAGE.EVENT_BUS, {
type: 'diceConditionAdd',
data: item
})
this.$router.back()
})
}
} else {
this.$router.back()
}
}
}

}



