
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import Scroll from '@/components/scroll'
import LoadStatusMixin from '@/js/mixin/loadStaus'
import BarChangeMixin from '@/js/mixin/barChange'
export default {
components: {
Cell,
Group,
XSwitch,
CellFormPreview,
Scroll
},
data () {
return {
}
},
mixins:[LoadStatusMixin,BarChangeMixin],
created () {
this.goodnessList()
},
methods: {
changeData(data) {
let obj1 = {
label: '活动名称',
value: data.activity_info.activity_name
}
let obj2 = {
label: '得票数',
value: data.vote_count
}
let obj3 = {
label: '排名',
value: data.ranking
}
let objArr = [obj1, obj2, obj3]
Object.assign(objArr,data)
return objArr
},

goodnessList () {
this.onloadStart()
this.$api.getGoddessList(this.$barId).then((data) => {
this.list=[]
for (let i = 0; i < data.length; i++) {
this.list.push(this.changeData(data[i]))
}
this.onLoadSuccess()
})
},

onWall (item ,index) {
let status = item.is_wall === 1?2:1
let data = {
'is_wall': status
}
this.$api.setGoddessWall(this.$barId, item.id, data).then((res) => {
item.is_wall = status
this.$util.change1(this.list,item)
})
},
onBarChange(){
this.goodnessList()
}
},
filters: {
capitalize: function (value) {
switch (value) {
case 1:
return '未上墙'
break
case 2:
return '已上墙'
break
}
}
}
}



