export default{
  props: {
    isNext: {
      type: Boolean,
      default:false
    }
  },
  methods:{
    save(){
      this.$emit('increment',true)
    },
    cancel(){
      this.$emit('increment',false)
    },
    next(){
      this.$emit('next')
    }
  }
}




