
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import LoadImg from '@/components/LoadImg'
import BarChangeMixin from '@/js/mixin/barChange'
export default {
name:'ExtentsionNoKeep',
components: {
Cell,
Group,
LoadImg
},
data () {
return {
value: '',
img:''
}
},
mixins:[BarChangeMixin],
created () {
this.getActivity()
},
methods: {
getActivity () {
let that = this
this.$api.getLatestGoddessDetail(this.$barId).then((data) => {
console.log(data)
if (data){
this.value = data.goddess_url
this.getQrcodeImg()
}else {
this.$vux.alert.show({
title: '提示',
content: '当前无进行中的票选活动',
onHide () {
that.$router.back()
}
})
}
})
},

getQrcodeImg () {
let data = {
'url': this.value,
'qiniu_key': `${encodeURIComponent(this.value).replace(/[^0-9a-zA-Z]|http|https/g,'')}.jpg`,
'qrcode_type': 2,
'size': 400
}
this.$api.getQrcode(data).then((res) => {
this.img = res.qiniu_key
})
},
onCopy: function (e) {
this.$vux.alert.show({
title: '提示',
content: '已复制到剪贴板'
})
},
onBarChange(){
this.value=''
this.img=''
this.getActivity()
}
}
}



