
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
name: "OccupyFree",
data() {
return {
isAll:false,
isAdmin:false,
isNoBody:false,
}
},
created(){
let type = this.$store.state.currentBar.publish_pic_type
this.isAll = type === 1
this.isAdmin = type === 2
this.isNoBody = type === 3
},
components: {
XSwitch,
Group
},
computed: {
status(){
return this.$store.state.currentBar.publish_pic_type
}
},
watch:{
status(newVal){
let type = newVal
this.isAll = type === 1
this.isAdmin = type === 2
this.isNoBody = type === 3
}
},
methods:{
onClick(type,newVal){
console.log(type,newVal)
if (!newVal){
let data = {
'publish_pic_type':type
}
this.$api.changeBarInfo(this.$barId,data)
.then(data=>{
this.$store.commit(this.$config.STORAGE.BAR_INFO,data)
})
}
}
}
}



